function ReactDOM() {}

ReactDOM.render = function (vnode, container) {
  if (!container || !container.nodeType) {
    console.error("error: container must be an element");
    return;
  }

  container.innerHTML = "";

  return render(vnode, container);
};

// è®¾ç½®å±žæ€§
function setAttribute(dom, name, value) {
  // å¦‚æžœæ˜¯ç±»å
  if (name === "className") name = "class";

  // å¦‚æžœæ˜¯äº‹ä»¶ç›‘å¬äº‹ä»¶ï¼šonxxx
  if (/on\w+/.test(name)) {
    name = name.toLowerCase();
    dom[name] = value;
  } else if (name === "style") {
    // å¦‚æžœæ˜¯æ ·å¼ï¼ŒåŒæ—¶æ˜¯å­—ç¬¦ä¸²
    if (!value || typeof value === "string") {
      dom.style.cssText = value || "";
    } else if (value && typeof value === "object") {
      // å¦‚æžœstyleæ˜¯å¯¹è±¡ï¼Œåˆ™è½¬æ¢ä¸ºå­—ç¬¦ä¸²
      for (let key in value) {
        // å¦‚æžœæ˜¯æ•°å€¼åˆ™åŠ ä¸Špxå•ä½
        dom.style[key] =
          typeof value[key] === "number" ? value[key] + "px" : value[key];
      }
    }
  } else {
    // æ™®é€šå±žæ€§åˆ™ç›´æŽ¥è®¾ç½®
    dom[name] = value;

    if (value) {
      dom.setAttribute(name, value);
    } else {
      dom.removeAttribute(name);
    }
  }
}

// æ¸²æŸ“ä¸»å‡½æ•°
function render(vnode, container) {
  // å¦‚æžœæ˜¯çº¯æ–‡æœ¬
  if (typeof vnode === "string") {
    const textNode = document.createTextNode(vnode);
    container.appendChild(textNode);
    return;
  }

  const dom = document.createElement(vnode.tag);

  if (vnode.attrs) {
    Object.keys(vnode.attrs).forEach((attr) => {
      const value = vnode.attrs[attr];
      setAttribute(dom, attr, value); // è®¾ç½®å±žæ€§
    });
  }

  vnode.children &&
    vnode.children.length &&
    vnode.children.forEach((child) => render(child, dom));

  return container.appendChild(dom);
}

export default ReactDOM;
